  width: 0%;
  height: 0.2rem;
  position: absolute;
  bottom: 8px;
  left: 25%;
  transition: all 0.4s linear;
  background-image: linear-gradient(to right, #ff6464, #ff3b3b);
  box-shadow: 0 0 20px 0 #fbbd61;
}

.navbar .navbar-collapse .navbar-nav .nav-item:hover::after {
  width: 50%;
}

